<template>
  <header class="container-fluid bg-dark text-white text-uppercase">
    <div
      class="d-flex align-items-center justify-content-between flex-column flex-md-row mb-4 py-2"
    >
      <h3 class="mb-0">{{ $t("header.title") }}</h3>
      <nav class="nav">
        <a
          class="nav-link text-white"
          :class="[locale === 'en' ? 'active' : '']"
          @click="switchLanguage('en')"
          >
          <span class="flag-icon flag-icon-us"></span>
          {{ $t("header.english") }}</a
        >
        <a
          class="nav-link text-white"
          :class="[locale === 'fr' ? 'active' : '']"
          @click="switchLanguage('fr')"
          >
          <span class="flag-icon flag-icon-fr"></span>
          {{ $t("header.french") }}</a
        >
      </nav>
    </div>
  </header>
</template>

<script>
import { useI18n } from "vue-i18n";
export default {
  name: "Header",
  setup() {
    const { t, locale } = useI18n();

    const switchLanguage = (lang) => {
      locale.value = lang;
    };

    return {
      switchLanguage,
      t,
      locale,
    };
  },
};
</script>

<style scoped>


.bg-dark {
  background-color: #14181D !important;
}
.nav-link {
  margin-right: 15px;
  font-weight: bold;
}

a {
  cursor: pointer;
}

.active {
  pointer-events: none;
  opacity: 0.5;
}



@media (max-width: 425px) {
  h3 {
    display: none;
  }

  nav {
    padding: 0rem;
  }
}

@media (min-width: 1441px) and (max-width: 2560px) {
  nav, h1 {
    font-size: 1.3rem; 
  }
  h3 {
    font-size: 1.7rem; 
  }
  p {
    font-size: 3.2rem;
  }
  .btn {
    font-size: 1.2rem;
  }
}

</style>

